import {ADD_REMINDER,DEL_REMINDER,CLEAR_REMINDER} from '../constants'

export const addReminder = (text,dueDate)=>{
    return {
        type:ADD_REMINDER,
        text,
        dueDate
    }
}

export const delReminder = (id)=>{
    return {
        type:DEL_REMINDER,
        id
    }
}

export const clearReminder = ()=>{
    return {
        type:CLEAR_REMINDER
    }
}




